import React from "react";

const pako = require("pako");

const compress = (input: string) => {
  const deflated = pako.deflate(input, { to: "string" }); //  Uint8Array(18) [120, 156, 171, 86, 74, 76, 76, 84, 178, 50, 52, 174, 5, 0, 17, 7, 2, 254]
  const base64Encoded = btoa(deflated); // 64
  return base64Encoded;
};

// atob(base64) 

export function decompress(input: string) {
  try {
    const base64Decoded = atob(input); //  "120,156,171,86,74,76,76,84,178,50,52,174,5,0,17,7,2,254"
    // inflateuint8Array
    const arr = base64Decoded.split(",");
    const uintArray = new Uint8Array(arr.map(Number));
    return pako.inflate(uintArray, { to: "string" });
  } catch {
    return;
  }
}

const Signature = ({
  squares,
  width,
}: {
  squares: boolean[];
  width: number;
}) => {
  return (
    <>
      <a
        href={
          "http://kijimad.github.io/golife?s=" +
          compress(JSON.stringify(squares)) +
          "&w=" +
          width
        }
      >
        
      </a>
    </>
  );
};

export default Signature;
